.countryInfo {
  font-family: 'Ubuntu Mono', monospace;
  font-size: 14px;
  font-weight: bold;
  margin: 0;
  margin-top: 0.125rem;
  margin-bottom: 0.125rem;
}
.pointInfo {
  font-family: 'Ubuntu Mono', monospace;
  font-size: 12px;
  margin: 0;
}
.count {
  font-family: 'Ubuntu Mono', monospace;
  font-size: 18px;
  width: 100%;
  margin: 0;
  text-align: right;
}
.box {
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 55px;
}
.countryBlock {
  position: relative;
  margin-bottom: 0.6rem;
  padding-left: 19px;
}
.countryBlock.row {
  display: flex;
  flex-direction: row;
  padding-left: 0;
}
.countryBlock.row .box {
  position: relative;
  top: unset;
  left: unset;
  width: 22px;
  height: 22px;
}
.countryBlock.row .block-1 {
  position: relative;
}
.countryBlock.row .block-3 {
  margin-top: 0;
  background: #eee;
}
.countryBlock.last {
  margin-bottom: 0;
}
.countryBlock.last:after {
  display: none;
}
.countryBlock:after {
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 1px;
  content: ' ';
  border-bottom: 1px dashed #ddd;
}
.block-1 {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: left;
  width: 35px;
  height: 35px;
  padding-left: 3px;
}
.block-2 {
  align-items: center;
  justify-content: center;
  flex: 1;
  padding-right: 0.825rem;
}
.block-3 {
  display: flex;
  align-items: center;
  justify-content: right;
  min-width: 50px;
  margin-top: 5px;
  padding-right: 0.425rem;
  padding-left: 0.825rem;
}
