.Main {
  padding: 1.5rem;
}
.Main > h1 {
  font-size: 24px;
  margin-top: 0;
  margin-bottom: 2rem;
}
.Main .selector {
  font-size: 14px;
  margin-bottom: 2rem;
}
.Main .selector :global(.item-renderer) input[type='checkbox'] {
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}
.Main.medium {
  padding: 2rem 2.5rem;
}
.Main.medium > h1 {
  font-size: 32px;
}
.label {
  font-size: 14px;
  font-weight: bold;
}
.footer {
  padding: 2rem;
  text-align: center;
}
.footer p {
  font-size: 12px;
}
.notes {
  font-size: 13px;
  padding: 0.5rem 1.5rem;
  background: #eee;
}
.title {
  font-size: 14px;
  font-weight: bold;
}
