.LineWrapper {
  position: relative;
  padding: 0;
}
.LineWrapper h2 {
  font-size: 18px;
  margin: 0.5rem 0 0.75rem;
}
.LineWrapper.medium {
  padding: 1rem 0.5rem;
}
.LineWrapper.medium .button {
  position: absolute;
  top: 1.5rem;
  right: 15px;
}
.LineWrapper.medium h2 {
  margin: 1rem 0 1.5rem;
}
.LineWrapper.medium .Controls .Field {
  display: inline-block;
  margin-right: 1.5rem;
  padding: 0 1rem;
}
.tooltip {
  padding: 0.5rem;
  border: 1px solid #ddd;
  background: #fff;
}
.Line {
  height: 600px;
}
.Field {
  display: block;
  max-width: 80%;
  margin-bottom: 1.25rem;
  padding: 0 0 0.5rem 0;
}
.Field.last {
  margin-bottom: 0;
  padding-bottom: 0;
}
.Field .label {
  font-size: 14px;
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}
.Field .text {
  font-size: 13px;
  margin-left: 0.625rem;
}
.Field input[type='number'] {
  font-family: 'Ubuntu Mono', monospace;
  font-size: 16px;
  display: block;
  flex: 1;
  min-height: 40px;
  padding-right: 0.5rem;
  text-align: right;
  border: 1px solid #ccc;
}
.Field .range {
  display: flex;
  flex-direction: row;
}
.button {
  font-size: 14px;
  padding: 0.5rem 0.8rem;
  color: #fff;
  border: none;
  background: #1494a5;
  -webkit-appearance: none;
}
.button .buttonLabel {
  margin-left: 0.5rem;
}
.Controls {
  margin: 1rem 0;
  padding: 1rem;
  background: #eee;
}
