.icon.withColor {
  color: #666;
}
.icon.withColor.highlight {
  color: #3da69c;
}
.icon.withColor.highlight svg {
  fill: #3da69c;
}
.icon.withColor svg {
  fill: #666;
}
.icon.withColor.disabled svg {
  fill: rgba(102, 102, 102, 0.5);
}
.icon.withColor.lighterForeground svg {
  fill: #fff;
}
.icon.withColor.lighterForeground.disabled svg {
  fill: rgba(255, 255, 255, 0.5);
}
.icon.withColor.lighterForeground {
  color: #fff;
}
.icon.withColor.lighterForeground.disabled {
  color: rgba(255, 255, 255, 0.5);
}
.icon.withColor.disabled {
  color: rgba(102, 102, 102, 0.5);
}
